import React, {Component} from 'react';
import about from '../assets/img/550x640.jpg';
import foto from '../assets/img/1.jpg';

export default class Skills extends Component {

  state = {
    show: false,
  };

  componentDidMount(){
    const elemento = document.getElementById('skills');
    window.addEventListener('scroll', (e) => {
      const top1 = elemento.offsetTop;
      const height = elemento.offsetHeight;
      const top = window.pageYOffset;
      if(top >= (top1 - height + 200)){
        this.setState({
          show: true
        })
      }
    })
  }

  render() {
    const idioma = this.props.strings;
    const open = this.state.show ? 'open' : '';

    return (
        <section id={'skills'}>
          <div className="skills-wrap">
            <div className="container">
              <div className="inner-wrap">
                <div className="leftbox">
                  <div className="mini-title-holder">
                    <h4>{idioma.abilities}</h4>
                  </div>
                  <p>{idioma.info}</p>
                </div>
                <div className="rightbox">
                  <div className="progress-bar-wrap-total">
                    <div className="progress-wrap" data-size="small" data-round="c" data-strip="off">
                      {
                        idioma.skills.map(skill => {
                          return (
                              <div className="progress" data-value={95} data-color="#000">
                                <span>
                                  <span className="label">
                                    {skill.skill}
                                  </span>
                                  <span className="number">{skill.percentage}%</span>
                                </span>
                                <div className="bar-bg">
                                  <div className={`bar-wrap ${open}`}>
                                    <div className="bar"
                                         style={{width: `${skill.percentage}%`, height: '100%', backgroundColor: 'rgb(0, 0, 0)'}}/>
                                  </div>
                                </div>
                              </div>
                          )
                        })
                      }
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
    )
  }
}