import React, {Component} from 'react';
import br from '../assets/img/br.svg';
import en from '../assets/img/en.svg';
import es from '../assets/img/es.svg';
import fr from '../assets/img/fr.svg';
import ge from '../assets/img/ge.svg';
import ru from '../assets/img/ru.svg';
import chevron from '../assets/img/chevron-left.svg';

export default class Cabecalho extends Component {

  state = {
    fechado: false,
    isActive: false,
    display: false,
    navigate: false,
    redirect: ''
  };

  alternar = (e) => {
    e.preventDefault();
    this.setState(prev => {
      const bool = !prev.fechado;
      this.props.full(bool);
      return {
        fechado: bool
      }
    })
  };

  alternarMobile = (e) => {
    e.preventDefault();
    const isActive = this.state.isActive;
    if (isActive) {
      setTimeout(() => {
        this.setState({
          display: false
        })
      }, 500);
    }
    this.setState(prev => {
      const {isActive} = prev;
      return {
        isActive: !isActive,
        display: true
      }
    })


  };

  changeLang = (event, lang) => {
    event.preventDefault();
    let location = window.location.pathname;
    location = `/${lang}${location.substr(3, location.length - 3)}`;
    console.log('mudando local -->>');
    console.log(location);
    window.location.href = location;
  };

  render() {
    const fechado = this.state.fechado ? 'fechado' : '';
    const words = this.props.words;
    const isActive = this.state.isActive ? 'is-active' : '';

    let brActive = '';
    let enActive = '';
    let esActive = '';
    let frActive = '';
    let geActive = '';
    let ruActive = '';

    switch (this.props.lang) {
      case 'br': brActive = 'active'; break;
      case 'en': enActive = 'active'; break;
      case 'es': esActive = 'active'; break;
      case 'fr': frActive = 'active'; break;
      case 'ge': geActive = 'active'; break;
      case 'ru': ruActive = 'active'; break;
      default: brActive = 'active'; break;
    }

    return (
        <div>
          <div className="cabecalho_mobile">
            <div className="main">
              <div className="logo"/>
              <div className="trigger">
                <div className={`hamburger hamburger--collapse-r ${isActive}`} onClick={this.alternarMobile}>
                  <div className="hamburger-box">
                    <div className="hamburger-inner"/>
                  </div>
                </div>
              </div>
            </div>
            <div className={`nav ${isActive}`}>
              <div className={`mob_menu`}>
                <ul className={`anchor_nav`}>
                  <li><a className={`anchor_nav`} href="">{words.home}</a></li>
                  <li><a className={`anchor_nav`} href="#about">{words.about}</a></li>
                  <li><a className={`anchor_nav`} href="#skills">{words.skills}</a></li>
                  <li><a className={`anchor_nav`} href="">{words.services}</a></li>
                  <li><a className={`anchor_nav`} href="">{words.portfolio}</a></li>
                  <li><a className={`anchor_nav`} href="">{words.contact}</a></li>
                  <li>
                    <div className={`lang ${isActive}`}>
                      <a href='#' onClick={(e) => {this.changeLang(e, 'br')}}>
                        <img className={brActive} src={br} alt="br" title="Português"/>
                      </a>
                      <a href='#' onClick={(e) => {this.changeLang(e, 'en')}}>
                        <img className={enActive} src={en} alt="en" title="English"/>
                      </a>
                      <a href='#' onClick={(e) => {this.changeLang(e, 'es')}}>
                        <img className={esActive} src={es} alt="es" title="Español"/>
                      </a>
                      <a href='#' onClick={(e) => {this.changeLang(e, 'fr')}}>
                        <img className={frActive} src={fr} alt="fr" title="Français"/>
                      </a>
                      <a href='#' onClick={(e) => {this.changeLang(e, 'ge')}}>
                        <img className={geActive} src={ge} alt="ge" title="Deutsch"/>
                      </a>
                      <a href='#' onClick={(e) => {this.changeLang(e, 'ru')}}>
                        <img className={ruActive} src={ru} alt="ru" title="русский"/>
                      </a>
                    </div>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <div className={`cabecalho_left ${fechado}`}>
            <div className="logo">
              <div className={'lang'}>
                <a href='#' onClick={(e) => {this.changeLang(e, 'br')}}>
                  <img className={brActive} src={br} alt="br" title="Português"/>
                </a>
                <a href='#' onClick={(e) => {this.changeLang(e, 'en')}}>
                  <img className={enActive} src={en} alt="en" title="English"/>
                </a>
                <a href='#' onClick={(e) => {this.changeLang(e, 'es')}}>
                  <img className={esActive} src={es} alt="es" title="Español"/>
                </a>
                <a href='#' onClick={(e) => {this.changeLang(e, 'fr')}}>
                  <img className={frActive} src={fr} alt="fr" title="Français"/>
                </a>
                <a href='#' onClick={(e) => {this.changeLang(e, 'ge')}}>
                  <img className={geActive} src={ge} alt="ge" title="Deutsch"/>
                </a>
                <a href='#' onClick={(e) => {this.changeLang(e, 'ru')}}>
                  <img className={ruActive} src={ru} alt="ru" title="русский"/>
                </a>
              </div>
            </div>
            <div className="lista_menu">
              <ul>
                <li><a href="#">{words.home}</a></li>
                <li><a href="#about">{words.about}</a></li>
                <li><a href="#skills">{words.skills}</a></li>
                <li><a href="#">{words.services}</a></li>
                <li><a href="#">{words.portfolio}</a></li>
                <li><a href="#">{words.contact}</a></li>
              </ul>
            </div>
            <a className={`fechar ${fechado}`} href="#" onClick={this.alternar}>
              <img src={chevron} alt={'<-'}/>
            </a>
          </div>
        </div>

    )
  }
}