import React, {Component} from 'react';
import port1 from '../assets/img/portfolio/1.jpg';
import port2 from '../assets/img/portfolio/2.jpg';
import port3 from '../assets/img/portfolio/3.jpg';
import port4 from '../assets/img/portfolio/4.jpg';
import port5 from '../assets/img/portfolio/5.jpg';
import port6 from '../assets/img/portfolio/6.jpg';
import port600 from '../assets/img/portfolio/600x600.jpg';
import HoverItem from "./hover-item";

export default class Portfolio extends Component {

  state = {
    itens: [],
    selecionado: 'All',
    categorias: new Set(),
    left: 0,
    top: 0,
    visible: false,
    item:{}
  };

  itens = [
    {
      title: 'Findog',
      img: port1,
      bigImg: port1,
      category: 'Web',
      date: 'September 12, 2018',
    },
    {
      title: 'São Geraldo',
      img: port2,
      bigImg: port2,
      category: 'Mobile',
      date: 'April 12, 2018',
    },
    {
      title: 'Tim RBT',
      img: port3,
      bigImg: port3,
      category: 'Web',
      date: 'Agoust 12, 2018',
    },
    {
      title: 'Place to Go',
      img: port4,
      bigImg: port4,
      category: 'Zert',
      date: 'December 12, 2019',
    },
  ];

  mudarCategoria = (e, categoria) => {
    e.preventDefault();
    this.setState({
      selecionado: categoria
    })
  };

  abrirPopup = (e, item) => {
    e.preventDefault();
    this.props.onClickItem(item);
    console.log(item);
  };

  componentWillMount() {
    const categorias = new Set();
    categorias.add('All');
    this.itens.forEach(item => {
      categorias.add(item.category);
    });
    this.setState({
      itens: this.itens,
      categorias: categorias
    });
  }

  render() {
    const idioma = this.props.strings;
    const categorias = [];
    this.state.categorias.forEach(item => {
      categorias.push(item)
    });
    const visible = this.state.visible ? 'visible' : '';

    return (
        <section id={'portfolio'}>
          <div className="portfolio-wrapper-all">
            <div className="second-portfolio">
              <div className="container">
                <div className="portfolio-wrap">
                  <div className="title-holder portfolio">
                    <h3>{idioma.my_works}</h3>
                    <span>{idioma.check_out}</span>
                  </div>
                  <ul className="portfolio-filter">
                    {
                      categorias.map(categoria => {
                        if (categoria === 'All') {
                          return (
                              <li >
                                <a href="#" className="current" onClick={(e) => { this.mudarCategoria(e, categoria) }}>{idioma.all}</a>
                              </li>
                          )
                        }
                        return (
                            <li><a href="#" className="current" onClick={(e) => {
                              this.mudarCategoria(e, categoria)
                            }}>{categoria}</a></li>
                        )
                      })
                    }
                  </ul>
                  <ul className="portfolio-list gallery-zoom">
                    {
                      this.itens.filter(item => {
                        if (this.state.selecionado === 'All') return item;
                        if (item.category === this.state.selecionado) return item
                      }).map(item => {
                        return (
                            <li className="design">
                              <HoverItem item={item} />
                            </li>
                        )
                      })
                    }
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
    )
  }
}