import port600 from "../assets/img/portfolio/600x600.jpg";
import React, {Component} from "react";

export default class HoverItem extends Component{

  state = {
    visible: false,
    top: 0,
    left: 0
  };

  update = (evt) => {
    const x = evt.clientX;
    const y = evt.clientY;
    console.log(`x: ${x}, y: ${y}`);
    this.setState({
      top: y + 25,
      left: x -10
    })
  };

  onMouseEnter = (e, item) => {
    this.setState({
      visible: true,
      item: item
    });
    document.addEventListener('mousemove', this.update)
  };

  onMouseLeave = (e) => {
    document.removeEventListener('mousemove', this.update);
    this.setState({
      visible: false
    })
  };

  render(){
    const item = this.props.item;
    const visible = this.state.visible ? 'visible' : '';
    return (
        <div>
          <div className={`portfolio-titles ${visible}`} style={{left: this.state.left, top: this.state.top}}>
            {item.title}
            <span className="work--cat">Development</span>
          </div>
          <div className="entry portfolio-animation-wrap" onMouseEnter={(e) => {this.onMouseEnter(e, item)}} onMouseLeave={this.onMouseLeave}>
            <a href="#" onClick={(e) => {this.abrirPopup(e, item)}}>
              <img src={port600} alt/>
              <div className="portfolio-image-main" data-img-url={item.img}
                   style={{backgroundImage: `url(${item.img})`}}/>
            </a>
          </div>
        </div>

    )
  }
}