import React, {Component} from 'react';
import Cabecalho from "./cabecalho";
import {lang} from "../utils/strings";
import Hero from "./hero";
import About from "./About";
import Skills from "./Skills";
import Portfolio from "./Portfolio";
import Popup from "./Popup";

export default class Home extends Component{

  state = {
    cabecalho: lang.cabecalho.br,
    hero: lang.hero.br,
    about: lang.about.br,
    skills: lang.skills.br,
    portfolio: lang.portfolio.br,
    full: false,
    item: {},
    popupVisible: false
  };

  full = (bool) => {
    this.setState({
      full: bool
    })
  };

  onClickItem = (item) => {
    this.setState({
      popupVisible: true,
      item: item
    })
  };

  componentWillMount(){
    const match = this.props.match.params.lang;
    if(match){
      const selected = lang.cabecalho[match];
      const hero = lang.hero[match];
      const about = lang.about[match];
      const skills = lang.skills[match];
      const portfolio = lang.portfolio[match];
      this.setState({
        cabecalho: selected,
        hero: hero,
        about: about,
        skills: skills,
        portfolio: portfolio
      });
    }
  }

  render(){
    const full = this.state.full ? 'full' : '';
    return (
        <div>
          <Cabecalho words={this.state.cabecalho} lang={this.props.match.params.lang} full={this.full}/>
          <Popup strings={this.state.popup} visible={this.state.popupVisible} item={this.state.item} onClose={() => {this.setState({popupVisible: false})}} />
          <div className={`conteudo ${full}`}>
            <div className="rightpart_inner">
              <Hero strings={this.state.hero}/>
              <About strings={this.state.about}/>
              <Skills strings={this.state.skills}/>
              <Portfolio strings={this.state.portfolio} onClickItem={this.onClickItem}/>
            </div>
          </div>
        </div>
    )
  }
}

const styles = {
  hello:{
    marginLeft: 320,
    marginTop: 20
  }
};