import React, {Component} from 'react';
import hero from '../assets/img/hero.jpg'
import bg from '../assets/img/2.jpg';
import Typing from "react-typing-animation";

const SPEED = 100;

export default class Hero extends Component {

  state = {
    index: 0,
    i_am: 'Freelancer',
    strings: this.props.strings,
    current: '',
    typing: true
  };

  mudarPalavra = () => {
    const {words} = this.props.strings;
    let indice = this.state.index;
    const atual = this.props.strings.words[indice];
    indice++;
    if(indice === words.length) indice = 0;
    this.setState({
      current: atual,
      index: indice
    });
  };

  componentWillMount(){
    this.mudarPalavra();
  }

  render() {
    return (
        <section>
          <div className="hero-wrap">
            <div className="box-wrap">
              <div className="bg-wrap">
                <div className="overlay-image">
                  <img src={bg} alt="bg"/>
                </div>
                <div className="overlay-color hero"/>
              </div>
              <div className="content hero">
                <div className="inner-content">
                  <div className="image-wrap">
                    <img src={hero} alt/>
                  </div>
                  <div className="name-holder">
                    <h3>{this.props.strings.hygor}  <span>{this.props.strings.christian} </span></h3>
                  </div>
                  <div className="text-typing">
                    <p>
                      <span>{this.props.strings.i_am} </span>
                      <Typing className={'typing'} loop speed={30} onFinishedTyping={this.mudarPalavra} startDelay={50}>
                        <span className="animation-text-word">{this.state.current}</span>
                        <Typing.Backspace delay={2000} count={this.state.current.length - 1}/>
                        <Typing.Reset count={1}/>
                      </Typing>
                    </p>
                  </div>
                </div>
              </div>
              <div className="arlo_tm_arrow_wrap bounce anchor">
                <a href="#about"><i className="xcon-angle-double-down"/></a>
              </div>
            </div>

          </div>
        </section>
    )
  }
}